const axios = require("axios");

const api = axios.create({
    // baseURL: "http://localhost:8080/api/data",
    // baseURL: "https://13.124.192.65:8080/api/data",
    baseURL: "https://www.naverapi.p-e.kr:8000/api/data",
});

export const dataLap = {
    get: () => {
        return api.get("/");
    },
    post: (data) => {
        return api.post("/", data);
    },
    delete: () => {
        return api.delete("/");
    },
};
